import './App.css';
import React, { Component } from 'react';
import NewNote from './components/NewNote';
import ListNotes from './components/ListNotes';
import LogIn from './components/LogIn';
import LogOut from './components/LogOut';
import { withAuth0 } from '@auth0/auth0-react';
import Loading from './components/Loading';
import ReactNotification from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css';
import { store } from 'react-notifications-component';
import Nav from './components/Nav';
import GetStarted from './components/GetStarted';


class App extends Component {
  constructor() {
    super();
    this.state = {
      notes: [],
      lastIndex: 0,
      formDisplay: false,
      newNoteBtnDisplay: true,
      listDisplay: false,
      queryText: '',
      isLoading: true
    }

    this.fetchNotes = this.fetchNotes.bind(this);
    this.toggleDisplay = this.toggleDisplay.bind(this);
    this.addNote = this.addNote.bind(this);
    this.toggleList = this.toggleList.bind(this);
    this.searchNotes = this.searchNotes.bind(this);
    this.deleteNote = this.deleteNote.bind(this);
    this.editNote = this.editNote.bind(this);
  }

  fetchNotes() {
    this.setState({
      isLoading: true
    });

    fetch('https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/notes').then(response => {
      response.json().then(data => {
        let index = 0;
        const temp_notes = data.map(note => {
          note.noteId = index;
          index += 1;
          return note;
        })

        this.setState({ notes: temp_notes, lastIndex: index, isLoading: false });
        console.log(this.state);
      })
    })
  }

  componentDidMount() {
    this.fetchNotes();
  }

  toggleDisplay() {
    this.setState({
      formDisplay: !this.state.formDisplay,
      newNoteBtnDisplay: !this.state.newNoteBtnDisplay
    })
  }

  addNote(note) {
    note.noteId = this.state.lastIndex;
    let temp_notes = this.state.notes;
    temp_notes.unshift(note);

    this.setState({
      notes: temp_notes,
      lastIndex: this.state.lastIndex + 1
    })

    
    fetch('https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/new-note', {
      method: "POST",
      body: JSON.stringify(note),
      headers: {"Content-type": "application/json; charset=UTF-8"}
    })
  }

  deleteNote(note) {
    let url = 'https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/delete-note?id=';
    url = url.concat(note.noteId);
  
    fetch(url, {
      method: "POST"
    }).then(response => {
      console.log(response);
    })
    this.fetchNotes();
  }

  async editNote(changes) {
    let url = 'https://morning-anchorage-80357.herokuapp.com/https://note-taker-api.glitch.me/edit-note?id=';
    url = url.concat(changes.noteId);
    console.log(changes);
    await fetch(url, {
      method: "POST",
      body: JSON.stringify(changes),
      headers: {"Content-type": "application/json; charset=UTF-8"}
    }).then(response => {
      console.log(response);
    })

    this.displayNotification('note-edited');

    this.fetchNotes();
  }

  toggleList() {
    this.setState({
      listDisplay: !this.state.listDisplay
    })
  }

  searchNotes(text){
    this.setState({
      queryText: text
    })
  }

  displayNotification(type) {
    switch(type) {
      case 'note-added':
        store.addNotification({
          message: "Note added successfully.",
          type: "success",
          insert: "top",
          container: "top-center",
          animationIn: ["animate__animated", "animate__fadeIn"],
          animationOut: ["animate__animated", "animate__fadeOut"],
          dismiss: {
            duration: 3000,
            onScreen: true
          }
        });
        break;
      case 'note-deleted':
        store.addNotification({
          message: "Note deleted successfully.",
          type: "danger",
          insert: "top",
          container: "top-center",
          animationIn: ["animate__animated", "animate__fadeIn"],
          animationOut: ["animate__animated", "animate__fadeOut"],
          dismiss: {
            duration: 3000,
            onScreen: true
          }
        });
        break;
      case 'note-copied':
        store.addNotification({
          message: "Text copied to clipboard.",
          type: "info",
          insert: "top",
          container: "top-center",
          animationIn: ["animate__animated", "animate__fadeIn"],
          animationOut: ["animate__animated", "animate__fadeOut"],
          dismiss: {
            duration: 3000,
            onScreen: true
          }
        });
        break;
      case 'note-edited':
        store.addNotification({
          message: "Note edited successfully.",
          type: "success",
          insert: "top",
          container: "top-center",
          animationIn: ["animate__animated", "animate__fadeIn"],
          animationOut: ["animate__animated", "animate__fadeOut"],
          dismiss: {
            duration: 3000,
            onScreen: true
          }
        });
        break;
      case 'unable-to-edit':
        store.addNotification({
          message: "You must be the Tech Agent associated with the note to be able to edit.",
          type: "danger",
          insert: "top",
          container: "top-center",
          animationIn: ["animate__animated", "animate__fadeIn"],
          animationOut: ["animate__animated", "animate__fadeOut"],
          dismiss: {
            duration: 3000,
            onScreen: true
          }
        });        
    }
    
  }


  render() {
    const { isAuthenticated, isLoading, loginWithRedirect } = this.props.auth0;

    if(isLoading) {
      return <Loading />
    } 
    else if (!isAuthenticated) {
      return (
          <>
            {loginWithRedirect()}
          </>
        )
    } 
    else {
      // Filter notes using the search query
      let filteredNotes = this.state.notes;
      filteredNotes = filteredNotes.filter(note => {
        return(
          note['customerName'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
          note['orderNumber'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
          note['issue'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
          note['stepsTaken'].toLowerCase().includes(this.state.queryText.toLowerCase()) ||
          note['date'].toLowerCase().includes(this.state.queryText.toLowerCase())
        )
      })

      // Sort notes by noteId (to display the newest at the top)
      filteredNotes.sort((a, b) => {
        return b.noteId - a.noteId; 
      })

      return (
        <>
          <ReactNotification />
          <main className="container col-10 mt-5">
            <Nav
              newNoteBtnDisplay={this.state.newNoteBtnDisplay}
              toggleDisplay={this.toggleDisplay}  
              listDisplay={this.state.listDisplay}
              toggleList={this.toggleList}
            />
            <GetStarted 
              formDisplay={this.state.formDisplay}
              listDisplay={this.state.listDisplay}
            />
            <NewNote 
              formDisplay={this.state.formDisplay}
              toggleDisplay={this.toggleDisplay}
              addNote={this.addNote}
              displayNotification={this.displayNotification}
            />
            <ListNotes
              notes={filteredNotes}
              listDisplay={this.state.listDisplay}
              toggleList={this.toggleList}
              searchNotes={this.searchNotes}
              deleteNote={this.deleteNote}
              isLoading={this.state.isLoading}
              editNote={this.editNote}
              fetchNotes={this.fetchNotes}
              displayNotification={this.displayNotification}
            />
          </main>
        </>
      );
    }
  }
}

export default withAuth0(App);
